* {
  margin: 0;
  padding: 0;
}

.mainContainer {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 80%;
  width: 100%;
  min-width: 418px;
}

.bodyContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: stretch;
      align-items: stretch;
  -ms-flex-line-pack: center;
      align-content: center;
  height: 70%;
  width: 70%;
  margin-top: 5%;
}

.bodyInfo {
  height: 300px;
  width: 300px;
}

.title {
  font-size: 60px;
  text-align: center;
  margin-bottom: 4vh;
  letter-spacing: 5px;
  color: #84caf9;
  font-weight: bold;
}

.bodyHeader {
  color: #84caf9;
  text-transform: uppercase;
}

#bodyParagraph {
  text-align: center;
  letter-spacing: 1px;
  margin: 20px 50px;
  font-size: 12px;
  line-height: 30px;
}

ul {
  margin: 1.5em;
}

ul li {
  border-bottom: 1px solid #ccc;

  padding: 0.2em 0 0.2em 0.5em;
  font-weight: bold;
  color: #84caf9;
  list-style-type: none;
}

.imageContainer {
  background-image: url("./main_image.png");
  height: 300px;
  width: 300px;
  background-size: contain;
}



